.account {
  &__container {
    max-width: 1015px;
  }

  &__title {
    margin: 0;
    margin-top: 48px;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}

.tabs {
  &__panel {
  }

  &__nav {
    align-items: flex-end;
    margin: 0;
    padding: 0;

    &-btn {
      border: 1px solid #7a7a7a;
      border-bottom: 0;
      border-radius: 18px 18px 0 0;
      padding: 12px 45px;
      font-weight: 700;
      font-size: 14px;
      line-height: 17px;
      color: #7a7a7a;

      &:focus {
        color: var(--dark-color);
        background-color: inherit;
      }

      &--active {
        position: relative;
        border: 1px solid var(--dark-color);
        border-bottom: 0;
        border-radius: 18px 18px 0 0;
        padding: 17px 55px;
        color: var(--dark-color);

        &::after {
          content: "";
          position: absolute;
          right: 0;
          bottom: -30px;
          display: block;
          width: 100%;
          height: 30px;
          background-color: var(--light-color);
        }
      }
    }
  }
}

.tabs-section-footer {
  margin-top: 32px;
  padding: 0 28px;

  &__info {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;

    &::before {
      content: url("../img/tooltip-black.svg");
      margin-right: 26px;
      width: 20px;
      height: 20px;
    }
  }

  &__connection {
    padding-left: 47px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
  }
}

.tabs-section-body {
  border: 1px solid var(--dark-color);
  border-radius: 18px;
  border-top-left-radius: 0;
  padding: 40px 28px;
}

.account-tabs {
  &__nav {
    position: relative;

    &-btn {
      &::before {
        display: none;
