.product-accordion {
  width: 100%;
  transition: 0.4s;

  &::after {
    content: url("../img/arrow-down.svg");
    float: right;
    margin-left: 13px;
    width: 24px;
    height: 24px;
  }

  &--other {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;

    &::after {
      content: url("../img/arrow-down.svg");
      float: left;
      margin-right: 12px;
      margin-left: 13px;
      width: 24px;
      height: 24px;
    }
  }

  &--product {
    padding-right: 30px;
    padding-left: 11px;

    &::after {
      content: "";
      display: none;
    }
  }
}

.active,
.product-accordion:hover {
  border: 1px solid #0089c8;
  background-color: #eefcfe;
}

.active::after {
  content: url("../img/arrow-up.svg");
}

.accordion-panel {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.2s ease-out;
}

.ui-accordion-header-icon {
  content: url("../img/arrow-down.svg");
}

.ui-icon-triangle-1-s {
  content: url("../img/arrow-up.svg");
}

.ui-accordion-header-active {
  border: 1px solid #0089c8;
  background-color: #eefcfe;
}
