.input {
  border: 1px solid #bababa;
  border-radius: 12px;
  padding: 18px 24px;
  width: 100%;
  font-weight: 600;
  font-size: 21px;
  line-height: 25px;
  color: var(--dark-color);

  &::placeholder {
    font-weight: 600;
    font-size: 21px;
    line-height: 25px;
    color: var(--dark-color);
  }
}
