/* stylelint-disable color-function-notation */
.calendar {
  position: relative;

  &__input {
    border: none;
    border-radius: 20px;
    padding: 12px 10px;
    width: 100%;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
  }

  &__icon {
    position: absolute;
    right: 7px;
    top: 10px;
    width: 22px;
    height: 22px;
    background-image: url("../img/calendar.svg");
    background-position: center;
    background-size: 22px;
    background-repeat: no-repeat;
    pointer-events: none;
  }
}

.air-datepicker {
  border: 1px solid var(--alto-color);
  border-radius: 12px;
  box-shadow: 0 9px 14px rgba(0, 0, 0, 20%);
  font-size: 12px;
  background-color: var(--light-color);
}

.air-datepicker-body--day-name {
  display: none;
}

.air-datepicker-cell.-selected- {
  border: none;
  border-radius: 5px;
  color: #fff;
  background: var(--dark-color);
}

.air-datepicker-cell.-range-from- {
  border-radius: 5px;
}

.air-datepicker-cell.-in-range- {
  margin: 3px 0;
  border-radius: 0;
  background-color: var(--alto-color);
}

.air-datepicker-cell.-current- {
  font-weight: 700;
  color: var(--dark-color);
}
