.sidebar {
  width: 272px;
  height: auto;

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__btn {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    border: 1px solid var(--dark-color);
    border-radius: 12px;
    padding: 13px 12px 13px 24px;
    width: 100%;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    background-color: var(--light-color);

    &--sub {
      justify-content: center;
      border: 1px solid #bababa;
      padding: 2px;
      width: 248px;
      height: 52px;
    }
  }

  &__sublist {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 12px;
  }

  @include tablet {
    position: absolute;
    z-index: 100;
    border: 1px solid var(--alto-color);
    border-radius: 12px;
    padding: 15px;
    width: 300px;
    background-color: var(--light-color);
    transform: translateX(-310px);
    transition: transform 0.2s ease-in-out;
  }
}

.sidebar--active {
  transform: translateX(-25px);
}
